.example {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background-color: #fff;
  color: #2f2f37;
  max-width: 960px;
  padding: 16px;
}

.example code {
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
  background-color: #ff6b0a;
  color: #fff;
  padding: 2px 6px;
  border-radius: 2px;
}

.example .background {
  height: 240px;
  // ❗️❗️❗️ The path to images from a SASS file
  // is always the same as from the css folder.
  background-image: url('../images/logo.png');
  background-size: cover;
  background-position: center;
}


body{

  background-color: #000000;
background-image: linear-gradient(147deg, #000000 0%, #434343 74%);;
}

.gallery {
  padding: 12px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: -30px;
  margin-top: -30px;
  margin-bottom: 20px;
}
.gallery a {
margin-bottom: 30px;
margin-right: 30px;

  display: block;

}
.photo-card {
  margin-right: 30px;
  margin-bottom: 30px;
  height: 100%;
  width: 100%;
  position: relative;
  flex-basis: calc((100% - 120px) / 3);
box-shadow: 0.2rem 0.2rem 0.2rem rgba($color: #000000, $alpha: 0.4);
overflow: hidden;
border-radius: 10px;
color: red;
}



.photo-card:hover .info{
  transition: bottom, 250ms ease-in-out;
  bottom: 0px;
  }

.img-item {
  font-size: 30px;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  transition: transform 400ms ease-out;
}

.img-item:hover  {
  transform: scale(1.15);
}

.info {
  position: absolute;
  bottom: -80px;
  margin-top: 0;
  background-color: black;
  opacity: 0.7;
  width: 100%;
  display: flex;
  justify-content: space-around;



}

.info-item:not(:last-child) {
  justify-content: center;
  margin-right: 15px;

}
.info-svg {
  fill: grey;

}
svg {
  margin-right: 5px;
}
b {
  color: grey;
  font-size: 30px;
}
.search-form {
  display: flex;
  justify-content: center;
  justify-content: space-around;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 70px;
  margin-top: 50px;
}


#gradient{
  width:0;
  height:50px;
  margin:0 auto;
  border-radius:100%;
  -webkit-box-shadow: 0 50px 200px 150px rgba(255, 255, 255, 0.93);
  -moz-box-shadow: 0 50px 200px 150px rgba(255, 255, 255, 0.93);
  box-shadow: 0 50px 200px 150px rgba(255, 255, 255, 0.93);
}
.search-form{
  
  background-color: grey;
  padding:40px;
  width:50%;
  margin:100px auto;
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  box-sizing:border-box;
  border-radius:6px;

}
input[type="text"]{
  font-size: 40px;
  width:90%;
  height:70px;
  padding-left:15px;
  border-radius:6px;
  border:none;
  color:#939393;
  font-weight:500;
  background-color:white;
  -webkit-box-shadow:
      0 -2px 2px 0 rgba(199, 199, 199, 0.55),
      0 1px 1px 0 #fff,
      0 2px 2px 1px #fafafa,
      0 2px 4px 0 #b2b2b2 inset,
      0 -1px 1px 0 #f2f2f2 inset,
      0 15px 15px 0 rgba(41, 41, 41, 0.09) inset;
  -moz-box-shadow: 
      0 -2px 2px 0 rgba(199, 199, 199, 0.55),
      0 1px 1px 0 #fff,
      0 2px 2px 1px #fafafa,
      0 2px 4px 0 #b2b2b2 inset,
      0 -1px 1px 0 #f2f2f2 inset,
      0 15px 15px 0 rgba(41, 41, 41, 0.09) inset;
  box-shadow:
      0 -2px 2px 0 rgba(199, 199, 199, 0.55),
      0 1px 1px 0 #fff,
      0 2px 2px 1px #fafafa,
      0 2px 4px 0 #b2b2b2 inset,
      0 -1px 1px 0 #f2f2f2 inset,
      0 15px 15px 0 rgba(41, 41, 41, 0.09) inset;
}
button[type="submit"]{
  width:75px;
  height:60px;
  background-image:url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 10px 5px;
  background-color:transparent;
  -webkit-background-size:60px 60px;
  background-size:60px 60px;
  border:none;
  cursor:pointer;
}
input[type="text"]:focus{
  outline:0;
}


.js-spinner {
  @keyframes spinner-line-fade-more {
    0%, 100% {
      opacity: 0; /* minimum opacity */
    }
    1% {
      opacity: 1;
    }
  }
  
  @keyframes spinner-line-fade-quick {
    0%, 39%, 100% {
      opacity: 0.25; /* minimum opacity */
    }
    40% {
      opacity: 1;
    }
  }
  
  @keyframes spinner-line-fade-default {
    0%, 100% {
      opacity: 0.22; /* minimum opacity */
    }
    1% {
      opacity: 1;
    }
  }
  
  @keyframes spinner-line-shrink {
    0%, 25%, 100% {
      /* minimum scale and opacity */
      transform: scale(0.5);
      opacity: 0.25;
    }
    26% {
      transform: scale(1);
      opacity: 1;
    }
  }
}